<template>
  <div class="about">
    <div class="cont">
      <el-table :data="tableData" height="calc(100% - 40px)">
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="sex" label=""></el-table-column>
        <el-table-column prop="age" label=""></el-table-column>
        <el-table-column prop="cs1" label="1"></el-table-column>
        <el-table-column prop="cs2" label="2"></el-table-column>
        <el-table-column prop="date" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="{ row }">
            <el-button type="primary" size="small" icon="el-icon-printer" @click="openDialog(row)"> </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog title="" :visible.sync="dialogVisible" width="700px" top="3vh" :modal="true" :close-on-click-modal="false" center @close="cancles">
        <div class="dialogsC">
          <div class="measureSty" id="printMeasure">
            <div class="personSty">
              <div>
                {{ printInfo.cap }}
                <div>xxx</div>
              </div>
              <div class="personInfoSty">
                <div v-for="item in personInfo" :key="item.index">
                  <span>{{ item.name }}</span>
                  <span>{{ printInfo[item.val] }}</span>
                </div>
              </div>
            </div>
            <div class="tableSty">
              <span class="titNav">xx</span>
              <el-table border :data="printList">
                <el-table-column prop="treatmentNum" label=" " width="114">
                  <template slot-scope="{ row }"> {{ row.index }} </template>
                </el-table-column>
                <el-table-column prop="cs1" label="1"></el-table-column>
                <el-table-column prop="cs2" label="2"></el-table-column>
                <el-table-column prop="date" label=""></el-table-column>
              </el-table>
              <span class="tip">*5</span>
            </div>
            <div class="resSty">
              <span class="titNav"></span>
              <span></span>
            </div>
            <div class="docSty">
              <span>xx</span>
              <span>xx</span>
              <span>{{ printInfo.date }}</span>
            </div>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="cancles"> </el-button>
          <el-button type="primary" @click="printClk"> </el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import print from 'print-js'
import printCss from '@/util/printCss'
export default {
  directives: {
    print,
  },
  data() {
    return {
      tableData: [], //
      dialogVisible: false, //
      printList: [],
      personInfo: [
        { name: ' ', val: 'name' },
        { name: ' ', val: 'sex' },
        { name: '', val: 'date' },
        { name: ' ', val: 'age' },
      ],
      printInfo: {},
    }
  },
  methods: {
    // 
    searchs() {
      // xxxx().then((data) => {
      const rs = [
        { cap: 'xxx1', name: '', sex: '', age: '22', cs1: '0.7', cs2: '1.1', date: '2024-09-21 12:33:23' },
        { cap: 'xxx2', name: '', sex: '', age: '28', cs1: '1.5', cs2: '0.6', date: '2024-08-20 12:33:23' },
        { cap: 'xxx3', name: '', sex: '', age: '34', cs1: '0.3', cs2: '1.9', date: '2024-09-24 12:33:23' },
        { cap: 'xxx4', name: '', sex: '', age: '18', cs1: '1.2', cs2: '1.3', date: '2023-09-20 12:33:23' },
        { cap: 'xxx5', name: '', sex: '', age: '47', cs1: '0.8', cs2: '0.9', date: '2024-03-20 12:33:23' },
        { cap: 'xxx6', name: '', sex: '', age: '19', cs1: '1.7', cs2: '0.4', date: '2024-09-12 12:33:23' },
        { cap: 'xxx7', name: '', sex: '', age: '29', cs1: '0.9', cs2: '1.5', date: '2024-09-3 12:33:23' },
        { cap: 'xxx8', name: '', sex: '', age: '33', cs1: '1.1', cs2: '1.4', date: '2024-09-4 12:33:23' },
        { cap: 'xxx9', name: '', sex: '', age: '41', cs1: '0.4', cs2: '1.7', date: '2024-09-23 12:33:23' },
        { cap: 'xxx10', name: '', sex: '', age: '25', cs1: '0.5', cs2: '1.0', date: '2024-09-31 12:33:23' },
      ]
      this.tableData = rs
      // })
    },
    // 
    cancles() {
      this.dialogVisible = false
    },
    // 
    openDialog(row) {
      this.printInfo = row
      // 5
      this.printList = this.tableData.slice(-5).map((k, i) => {
        return { ...k, index: i + 1 }
      })
      this.dialogVisible = true
    },
    // 
    printClk() {
      // css.measureSty
      const styleSheets = document.styleSheets
      let cssStr = ''
      Array.from(styleSheets).forEach((sheet) => {
        const rules = sheet.cssRules || sheet.rules
        Array.from(rules).forEach((rule) => {
          if (rule.cssText && rule.cssText.startsWith('.measureSty')) {
            cssStr += rule.cssText
          }
        })
      })
      // ,
      cssStr += printCss()
      this.$nextTick(() => {
        print({
          printable: 'printMeasure',
          type: 'html',
          style: cssStr,
          scanStyles: false,
        })
      })
    },
  },
  mounted() {
    this.searchs()
  },
}
</script>
